<template>
  <div class="container">
    <h2>Safe Regex</h2>
    <p>
      Detect potentially catastrophic exponential-time regular expressions by limiting the star
      height to 1.
    </p>
    <div class="ui card">
      <div class="content">
        <h4 class="ui sub header">Regex String</h4>
        <div class="ui input">
          <textarea
            placeholder="Enter Regex..."
            rows="6"
            spellcheck="false"
            v-model="regex"
          ></textarea>
        </div>

        <h4 class="ui sub header">Output</h4>
        <div class="ui input">
          <textarea placeholder="" rows="3" spellcheck="false" v-model="output"></textarea>
        </div>
      </div>
      <div class="extra content">
        <button class="ui primary button" @click="validate">
          Validate
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import safe from 'safe-regex';

export default {
  data() {
    return {
      regex: '',
      output: '',
    };
  },
  methods: {
    validate() {
      this.output = `${safe(this.regex)}`;
    },
  },
};
</script>

<style scoped>
.ui.card {
  width: 100%;
}

.ui.input,
.ui.input textarea {
  width: 100%;
}

.ui.input textarea {
  resize: vertical;
  padding: 0.5rem;
}
</style>
