<template>
  <div>
    <sui-menu pointing secondary>
      <a
        is="sui-menu-item"
        v-for="item in items"
        :active="isActive(item)"
        :key="item"
        :content="item"
        @click="select(item)"
      />
    </sui-menu>

    <div class="ui segment">
      <div v-if="this.active === 'SafeRegex'">
        <SafeRegex />
      </div>
      <div v-else></div>
    </div>
  </div>
</template>

<script>
import SafeRegex from '@/components/SafeRegex.vue';

export default {
  name: 'Regex',
  components: {
    SafeRegex,
  },
  data() {
    return {
      active: 'SafeRegex',
      items: ['SafeRegex'],
    };
  },
  methods: {
    isActive(name) {
      return this.active === name;
    },
    select(val) {
      this.active = val;
    },
  },
};
</script>
